import { Component, OnInit, OnDestroy } from '@angular/core';
import { Acknowledgement } from '@app/core/models';
import { Observable ,  BehaviorSubject } from 'rxjs';
import { DatePipe } from '@angular/common';
import { debounceTime ,  distinctUntilChanged, take } from 'rxjs/operators';
import { MatDialog, MatDialogRef } from '@angular/material';
import { AddAcknowledgementComponent } from '../../../components';
import { AcknowledgementCardComponent } from '../acknowledgement-card/acknowledgement-card.component';


@Component({
  selector: 'app-acknowledgements',
  templateUrl: './acknowledgements.component.html',
  styleUrls: ['./acknowledgements.component.less'],
    providers: [DatePipe, AcknowledgementCardComponent]
})
export class AcknowledgementsComponent implements OnInit, OnDestroy {
    private ackList: BehaviorSubject<Acknowledgement[]> = new BehaviorSubject([]);
    public readonly acknowledgements: Observable<Acknowledgement[]> = this.ackList.asObservable();
    searchTerm: BehaviorSubject<string> = new BehaviorSubject('');

    constructor(acknowledgement: Acknowledgement,
                private dialog: MatDialog) {

        acknowledgement.query().pipe(take(1)).subscribe((x: Acknowledgement[]) => {

            x = this._sort_list(x);
          
            this.ackList.next(x);
        });

        this.searchTerm.pipe(
            debounceTime(500),
            distinctUntilChanged()
        )
        .subscribe((x: string) => {
            if (x) {
                acknowledgement.query({q: x}).pipe(take(1)).subscribe(
                    (results: Acknowledgement[]) => {
                        const currentList: Acknowledgement[] = this.ackList.value;
                        const keys: number[] = currentList.map((p) => p.id);
                        let newList: Acknowledgement[] = currentList.concat(results.filter((v) => !keys.includes(v.id)));

                        // Sort by id;
                        newList = this._sort_list(newList);

                        this.ackList.next(newList);
                    }
                );
            }
            
        });
    }

    ngOnInit() {
    }

    ngOnDestroy() {
        this.searchTerm.unsubscribe();
        this.ackList.unsubscribe();
    }

    updateStage($event: number, status: string): void {

        let acknowledgement: Acknowledgement;
        acknowledgement = this.ackList.value.filter((x) => x.id === $event)[0];

        acknowledgement.status = status;
        acknowledgement.save();

        // Update the current list of purchase orders
        let updatedList: Acknowledgement[] = this.ackList.value.filter((x: Acknowledgement): boolean => x.id !== $event);
        updatedList.push(acknowledgement);

        updatedList = this._sort_list(updatedList);
        
        this.ackList.next(updatedList);
    }

    getTotal(status: string): number {
        const filteredList: Acknowledgement[] = this.ackList.value.filter((x) => x.status.toLowerCase() === status);
        const totals: number[] = filteredList.map((x) => Number(x.total));
        const total: number = totals.length > 0 ? totals.reduce((sum, y) => sum + y) : 0;
        return total;
    }

    public showAddAcknowledgement(): void {
        const dialogRef: MatDialogRef<AddAcknowledgementComponent> = this.dialog.open(AddAcknowledgementComponent, {
            width: '90vw',
        });

        dialogRef.beforeClose().pipe(take(1)).subscribe((result: Acknowledgement) => {
            if (result) {
                if (result instanceof Acknowledgement) {
                    const currentList: Acknowledgement[] = this.ackList.getValue();
                    const newList: Acknowledgement[] = currentList.concat(
						[result]
					);

					this.ackList.next(newList);
				}
            }

        });
    }

    private _sort_list(list: Acknowledgement[]): Acknowledgement[] {
        return list.sort((a: Acknowledgement, b: Acknowledgement): number => a.id - b.id);
    }
}
