import { Component, OnInit, Input } from '@angular/core';
import { MatCard, MatDialog } from '@angular/material';
import { DatePipe } from '@angular/common';
import { Acknowledgement } from '@app/core';
import { AcknowledgementDialogComponent } from '../../../components';

@Component({
    selector: 'acknowledgement-card',
    templateUrl: './acknowledgement-card.component.html',
    styleUrls: ['./acknowledgement-card.component.less'],
    providers: [MatCard, DatePipe]
})
export class AcknowledgementCardComponent implements OnInit {

    @Input() 
    acknowledgement: Acknowledgement | undefined;
    
    constructor(private dialog: MatDialog) {

    }

    ngOnInit() {
    
    }

    public showAcknowledgement(): void {
        this.dialog.open(AcknowledgementDialogComponent, {width: '90vw', data: {acknowledgement: this.acknowledgement }});
    }

}
